import {useState,useEffect} from 'react';
import "../Styles/AdministrationCss/ItemCommande.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import Col from "react-bootstrap/esm/Col";
import Row from "react-bootstrap/esm/Row";
import Table from 'react-bootstrap/Table';
import { Button } from 'react-bootstrap';
import CommandeDetail from './CommandeDetail';

//icons


function ItemCommande(props){

    const [error, setError] =useState(null);
    const [isLoaded, setIsLoaded] =useState(false);
    const [Commandes,setCommandes] = useState([]);
    const [idCommandesFordetail,setIdCommandes]=useState("");
    const rechercheTerm = props.searchtext;

     //For modalcommandeDetail
     const [modalDetailShow, setModalDetailShow] =useState(false);

    // Remarque : le tableau vide de dpendances [] indique
        // que useEffect ne sexcutera quune fois, un peu comme
        // componentDidMount()
        useEffect(() => {
            fetch("http://api/Api_Commande.php")
              .then(res => res.json())
              .then(
                (result) => {
                  setIsLoaded(true);
                  setCommandes(result.resultat);
                },
                // Remarque : il faut grer les erreurs ici plutt que dans
                // un bloc catch() afin que nous navalions pas les exceptions
                // dues  de vritables bugs dans les composants.
                (error) => {
                  setIsLoaded(true);
                  setError(error);
                }
              )
          }, [])
  
          if (error) {
                  return <div>Erreur : {error.message}</div>;
          } 
          else if (!isLoaded) {
                  return <div>Chargement...</div>;
          } 
          else {
            return(
                <Row>
                    <Col>
                        <Table striped hover>
                            <thead>
                                <tr>
                                <th>id</th>
                                <th>NomClient</th>
                                <th>PrenomClient</th>
                                <th>AdresseClient</th>
                                <th>TelephoneClient</th>
                                <th>Date/HeureCommande</th>
                                <th>Dtail</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                Commandes.Commandes.filter((recherchevalue)=>{
                                    return (recherchevalue.nomClient.toLowerCase().includes(rechercheTerm) || 
                                    recherchevalue.prenomClient.toLowerCase().includes(rechercheTerm) || 
                                    recherchevalue.adresseClient.toLowerCase().includes(rechercheTerm) ||
                                    recherchevalue.telephoneClient.includes(rechercheTerm)
                                    )
                                }).map((Commande,index)=>(
                                        <tr key={index}>
                                            <td>{Commande.id}</td>
                                            <td>{Commande.nomClient}</td>
                                            <td>{Commande.prenomClient}</td>
                                            <td>{Commande.adresseClient}</td>
                                            <td>{Commande.telephoneClient}</td>
                                            <td>{Commande.dateCommande}</td>
                                            <td>
                                            <Button className="btn btn-danger btn-sm p-1 m-0"
                                            onClick={() =>
                                                {
                                                    setModalDetailShow(true);
                                                    setIdCommandes(Commande.id);
                                                }}
                                            >
                                                detail
                                            </Button>
                                            </td>
                                        </tr>
                                ))}
                            </tbody>
                        </Table>
                    </Col>
                    <CommandeDetail
                        show={modalDetailShow} 
                        onHide={() => setModalDetailShow(false)} 
                        idcommande={idCommandesFordetail}
                        commandeinfo={Commandes}
                    />
                </Row>
            );
          }
}
export default ItemCommande;